.Board {
	margin: 3em auto;
	box-shadow: 0 0 10px 2px rgba(68, 181, 226, 0.99);
	border-radius: 2px;
}

.reset-game {
	padding: 0.5em 1em;
	font-size: 1.2em;
	background-color: rgba(68, 181, 226, 0.99);
	outline: none;
	border: rgba(68, 181, 226, 0.99);
	border-radius: 8px;
	box-shadow: 0 0 5px 2px rgba(68, 181, 226, 0.99);
	transition: all 0.3s ease-in;
}
.reset-game:hover {
	background-color: rgb(39, 34, 34);
	color: white;
}
.reset-game:active {
	transform: scale(0.95);
}
.Board-title {
	font-size: 2.5em;
	width: auto;
}
.title {
	color: rgba(212, 96, 1, 0.897);
	text-shadow: 1px 0.2px rgba(68, 181, 226, 0.4);
}
